
.Projets
{
    background-color: #3D5A80;
    .GallerieProjets
    {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: column;
        

        box-sizing: border-box;
        padding-top: 200px;
        //margin: 5em 2em;

        li:nth-child(even)
        {
            background-color: #98C1D9;
            h2
            {
                color: #293241;
            }
        }
        li:nth-child(odd)
        {
            background-color: #293241;
            h2
            {
                color: #EE6C4D;
            }
        }
    }
}
