html 
{
	scroll-behavior: smooth;
}
*
{
    margin: 0;
    z-index: 0;
    overflow-x: hidden;
    font-family: mPlus;
    font-weight: 400;
}

a
{
    text-decoration: none;
    color: inherit;
}

a:hover
{
    color: #3D5A80;
}

