
.Projet
{
    height: 500px;
    width: 94vw;
    padding-top: 30px;
    padding-left: 30px;
    box-sizing: border-box;

    margin: 0 auto;

    display: flex;
    flex-direction: row;

    margin-bottom: 150px;

    .Screenshot
    {
        overflow: hidden;
        width: 600px;
        justify-self: flex-start;
        align-self: flex-start;
        img
        {
            -webkit-user-select: none;
            -khtml-user-select: none;
            -moz-user-select: none;
            -o-user-select: none;
            user-select: none;
            width: 100%;
            max-height: 600px;
        }
    }
    .info
    {
        justify-self: flex-end;
        align-self: flex-start;
        padding-left: 200px;
        padding-top: 1vw;

        text-align: left;

        h2
        {
            text-shadow: 1px 1px #252525;
        }
    }
}